	@ViewChild('changeChequeDateDetailsPopUpTemplate') private changeChequeDateDetailsPopUpTemplate;

	lrDtoList: Array<LRDto> = [];

	bankLrDtoList: Array<LRDto> = [];
	bankDtoList: Array<LRDto> = [];
